<template>
  <div class="home">
    <Carousel :isTop="isTop"></Carousel>
    <div class="container">
      <div class="row mt-3">
        <div class="col-md-6">
          <InfoBlock category="news"></InfoBlock>
        </div>
        <div class="col-md-6">
          <InfoBlock category="academy"></InfoBlock>
        </div>
      </div>
      <Swiper title="" :products="hotProducts" class="my-3"></Swiper>
      <Swiper
        title=""
        :products="filtedProducts('', '')"
        class="my-3"
      ></Swiper>
      <Swiper
        title=""
        :products="filtedProducts('/', '/')"
        class="my-3"
      ></Swiper>
      <Swiper title="" :products="filtedProducts('')" class="my-3"></Swiper>
    </div>
  </div>
</template>

<script>
import Carousel from '@/components/fronted/Carousel.vue';
import Swiper from '@/components/fronted/Swiper.vue';
import InfoBlock from '@/components/fronted/InfoBlock.vue';

export default {
  props: {
    products: Array,
    cart: Array,
    coupon: Object,
  },
  components: {
    Carousel,
    Swiper,
    InfoBlock,
  },
  data() {
    return {
      isTop: true,
    };
  },
  methods: {
    updateScroll() {
      if (window.scrollY) {
        // 
        this.isTop = false;
      } else {
        this.isTop = true; // 
      }
    },
    filtedProducts(...category) {
      return this.products.filter((product) => category.includes(product.category));
    },
  },
  computed: {
    hotProducts() {
      return this.products.filter((product) => product.options.hot === true);
    },
  },
  mounted() {
    //  scroll 
    window.addEventListener('scroll', this.updateScroll);
  },
};
</script>

<style></style>
